import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Constants} from '../../../app.constants';
import {UserDataService} from '../../../services/user.service';
import {SurveyService} from '../../../services/survey.service';
import {IComment, IGood, ISurveyReport, ISurveyStructure, IUser} from '../../../interfaces/interfaces';
import {MarketSurveyService} from '../../../services/market-survey.service';
import {AuthService} from '../../../services/auth.service';
import {AppData} from '../../../data';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-view-survey',
  templateUrl: './view-survey.component.html',
  styleUrls: ['./view-survey.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ViewSurveyComponent implements OnInit {
  url: string;
  report: ISurveyReport = null;
  errorMessage = '';
  loading = false;
  good: IGood[];
  savingComment = false;

  public survey = [];
  public NewCommentForm: FormGroup;
  public price: any[];
  public markup: any[];
  initials: string;
  me: IUser;
  commentInitials: string;
  successMessage = '';
  show = false;
  showRequestApproval = false;
  showApproval = false;
  surveyStructure: ISurveyStructure = null;


  constructor(private userDataService: UserDataService,
              private formBuilder: FormBuilder,
              private oldSurveyService: SurveyService,
              private route: ActivatedRoute,
              private router: Router,
              private surveyService: MarketSurveyService, private authService: AuthService) {
  }


  ngOnInit() {
    this.buildForm();
    const id = this.route.snapshot.params['id'];
    this.getReport(id);
  }

  can(permission) {
    return AuthService.can(permission);
  }

  get permissions() {
    return AppData.permissions;
  }

  getReport(id: number) {
    this.loading = true;
    this.surveyService.getSurveyByID(id).subscribe((report: ISurveyReport) => {
      this.report = report;
      this.report.structure_ = JSON.parse(this.report.structure);
      console.log(this.report.structure_);

      if (this.report.comments !== undefined) {
        this.report.comments.forEach((comment) => {
          const commentNameBadge = comment.commentor ? comment.commentor.full_name : 'AN';
          const initials = commentNameBadge.match(/\b\w/g) || [];
          comment.commentInitials = ((initials.shift() || '') + (initials.pop() || '')).toUpperCase();
        })
      } else {
        this.report.comments = [];
      }

      this.NewCommentForm.patchValue({
        model_id: this.report.id
      });
      this.loading = false;

      if (this.report) {
        this.showApproval = !this.report.approved_by && AuthService.can(this.permissions.approve_survey_report);
        this.showRequestApproval = !this.report.approved_by && !AuthService.can(this.permissions.approve_survey_report) && this.report.request_approval;
        this.url = Constants.url;
      }

    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
      this.loading = false;
    });
  }

  buildForm() {
    this.NewCommentForm = this.formBuilder.group({
      comment: ['', Validators.compose([Validators.required])],
      model_id: ['', Validators.required]
    });
  }

  onSubmit({value, valid}: { value: IComment, valid: boolean }) {
    document.getElementById('close-button').click();
    this.savingComment = true;
    this.errorMessage = '';
    this.successMessage = '';

    this.oldSurveyService.addComment(value).subscribe(
      (resp: any) => {
        if (resp) {
          Constants.succeeded('You have just added a new comment!');
          this.report.comments.push({
            commentor: {first_name: (Constants.getAuthenticatedUser() || {}).n},
            body: value.comment,
            created_at: new Date().toUTCString()
          });
          this.NewCommentForm.patchValue({comment: ''});
        } else {
          this.errorMessage = resp.statusText;
        }
        this.savingComment = false;
      }, err => {
        Constants.handleError(err, this.router, true);
        this.savingComment = false;
      }
    )
  }


  approveSurvey(id) {
    Swal.fire({
      title: 'Confirmation',
      text: 'Do you want to approve this report?',
      type: 'question',
      cancelButtonText: 'Cancel',
      showCancelButton: true,
      confirmButtonText: 'Yes Delete',
      showConfirmButton: true
    }).then(willUpdate => {
      if (willUpdate.value) {
        this.surveyService.approveSurvey(id).subscribe(updated => {
            Constants.succeeded('Report has been Approved!', 'Approved!');
            window.setTimeout(() => {
              window.location.reload();
            }, 2000);
          }, (err) => {
            Constants.handleError(err, this.router, true);
          }
        )
      }
    });
  }


  print(): void {
    const element = document.getElementById('printSection').innerHTML;
    console.log(element);
    Constants.processPrint(element);
  }

  showDetail(r: ISurveyStructure) {
    this.surveyStructure = r;
    document.getElementById('showInfoBut').click();
  }
}




