import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Constants} from '../../app.constants';
import {FormBuilder, FormGroup} from '@angular/forms';
import {ISurveyReport} from '../../interfaces/interfaces';
import {UserDataService} from '../../services/user.service';
import {MarketSurveyService} from '../../services/market-survey.service';
import {AuthService} from '../../services/auth.service';
import Swal from 'sweetalert2';

declare var $: any;

@Component({
    selector: 'app-survey',
    templateUrl: './survey.component.html',
    styleUrls: ['./survey.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class SurveyComponent implements OnInit {
    surveyReports: ISurveyReport[] = [];
    me: any = {};
    public survey: ISurveyReport;
    public searchText: string;
    role = localStorage.getItem('userRole');
    public p: any;
    toggle = false;
    selectedAll: any;
    loading: boolean;
    searchForm: FormGroup;
    deleting: false;

    public placeHolderMessage = '';
    initials: string;
    current_id: number;

    current_page: number;
    from: number;
    last_page: number;
    next_page_url: string;
    path: string;
    per_page: number;
    prev_page_url: string;
    to: number;
    total: number;
    errorMessage: any;
    model: any = {};
    searching = false;
    hasSomeItemsChecked = false;
    processingDelete = false;
    private lastUrl: any;
    private lastParam: any;


    constructor(private userDataService: UserDataService,
                public surveyService: MarketSurveyService,
                private route: ActivatedRoute,
                private router: Router,
                private authService: AuthService,
                private formBuilder: FormBuilder) {
    }

    ngOnInit() {
        this.searchForm = this.formBuilder.group({
            searchField: [''],
            fromDate: [''],
            toDate: [''],
        });
        this.getLoggedInUser();
        this.submitSearch();
    }

    submitSearch() {
        const searchText = this.searchForm.get('searchField').value;
        const fromDate = this.searchForm.get('fromDate').value;
        const toDate = this.searchForm.get('toDate').value;

        let param = '';
        if (searchText) {
            param += 'search=' + searchText;
        }

        if (fromDate) {
            if (param) {
                param += '&';
            }
            param += 'from=' + this.getTextFromDate(fromDate);
        }

        if (toDate) {
            if (param) {
                param += '&';
            }
            param += 'to=' + this.getTextFromDate(fromDate);
        }

        const perPage = $('#per-page-select').val();

        if (parseInt(perPage, 10)) {
            if (param) {
                param += '&';
            }
            param += 'per_page=' + perPage;
        }

        if (param) {
            param = '?' + param;
        }

        this.searchResult('', param);
    }

    searchResult(url, params) {
        this.searching = true;
        this.errorMessage = '';

        this.lastUrl = url;
        this.lastParam = params;

        this.surveyReports = [];
        this.surveyService.searchSurvey(url, params).subscribe((data: any) => {
                const arr = ((typeof data.data) !== 'undefined' && data.data.length > 0);
                if (arr) {
                    data.data.forEach(d => d.selected = false);
                }
                this.surveyReports = data.data;
                this.current_page = data.current_page;
                this.from = data.from;
                this.last_page = data.last_page;
                this.next_page_url = data.next_page_url;
                this.path = data.path;
                this.prev_page_url = data.prev_page_url;
                this.per_page = data.per_page;
                this.to = data.to;
                this.total = data.total;

                this.searching = false;
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
                this.searching = false;
            }
        )
    }

    getLoggedInUser() {
        this.me = Constants.getAuthenticatedUser() || {};
        this.current_id = this.me.i;
    }

    deleteReportSelected() {
    }

    deleteSurvey(id) {
        this.processingDelete = true;
        Swal.fire({
            title: 'Are you sure?',
            text: 'Do you really want to discard this survey report?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.surveyService.deleteSurvey(id).subscribe((status) => {
                    let index = -1, count = 0;
                    this.surveyReports.forEach((r) => {
                        if (r.id.toString() === id.toString()) {
                            index = count;
                        }
                        count++;
                    });
                    if (index > -1) {
                        this.surveyReports.splice(index, 1);
                    }
                    Constants.succeeded('Survey report has been deleted!', 'Deleted!');
                    this.processingDelete = false;
                }, (err) => {
                    Constants.handleError(err, this.router, true);
                    this.processingDelete = false;
                });
            } else {
                this.processingDelete = false;
            }
        })
    }

    checkAll(ev) {
        this.surveyReports.forEach(x => x.selected = ev.target.checked)
    }

    isAllChecked() {
        return this.surveyReports.every(_ => _.selected);
    }

    private getTextFromDate(date: any) {
        if (typeof date === 'object') {
            return date.year + '-' + date.month + '-' + date.day;
        }
        return date.toString();
    }


    updateSelectionChanged() {
        let hasSomeItemsChecked = 0;
        this.surveyReports.forEach(x => {
            if (x.selected) {
                hasSomeItemsChecked++;
            }
        });

        this.hasSomeItemsChecked = hasSomeItemsChecked > 1;
    }

    deleteSelected() {

    }

}
